.user-sidebar {
    width: 300px;
    background: #0d1117;
    color: white;
    height: 100vh;
    overflow-y: auto;
    padding: 10px;
    border-right: 1px solid #2f3338;
  }
  
  .user-list {
    margin-top: 10px;
  }
  
  .user-card {
    display: flex;
    gap: 10px;
    align-items: center;
    padding: 10px;
    border-radius: 8px;
    cursor: pointer;
  }
  
  .user-card:hover {
    background: #161b22;
  }
  
  .user-card img {
    width: 48px;
    height: 48px;
    border-radius: 50%;
  }
  
  .user-info p {
    margin: 0;
    font-size: 0.85rem;
    color: #8b949e;
  }
  